import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'
import store from './store/index.js'

Vue.config.productionTip = false

App.mpType = 'app'
//把vuex定义成全局组件
Vue.prototype.$store = store

const app = new Vue({
    ...App,
	store
})
app.$mount()